import React from 'react';
import PropTypes from 'prop-types';
import { FaMapMarkerAlt } from 'react-icons/fa';
import { useInputValue } from '../../../hooks/useInputValue';
import { Marker, Name, Position, OptionsWrapper, Option } from './styles';

export const MarkerComponent = ({
  internalId,
  title,
  position: { lat, lng },
  goHandler,
  updateMarker,
}) => {
  // eslint-disable-next-line no-unused-vars
  const [newTitle, setNewTitle, onTitleChange] = useInputValue(title);

  return (
    <Marker>
      {title && (
        <Name
          type="text"
          value={newTitle}
          onChange={(e) => {
            onTitleChange(e);
            updateMarker({ internalId, title: e.target.value });
          }}
        />
      )}
      <Position>{`LAT: ${lat}`}</Position>
      <Position>{`LNG: ${lng}`}</Position>
      <OptionsWrapper>
        <Option
          type="button"
          title="Go"
          onClick={() => goHandler({ lat, lng })}
        >
          <FaMapMarkerAlt />
        </Option>
      </OptionsWrapper>
    </Marker>
  );
};

MarkerComponent.propTypes = {
  internalId: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  position: PropTypes.shape({
    lat: PropTypes.number.isRequired,
    lng: PropTypes.number.isRequired,
  }).isRequired,
  goHandler: PropTypes.func.isRequired,
  updateMarker: PropTypes.func.isRequired,
};
