import React, { useContext } from 'react';
import MapContext from '../../Contexts/MapContext';
import { MarkerComponent } from './Marker/index';
import { Header, Title, MarkerList } from './styles';

const MarkersIndicator = () => {
  const { setCenter, setZoom, markers, updateMarker } = useContext(MapContext);
  return (
    <>
      <Header>
        <Title>Markers</Title>
      </Header>
      <MarkerList>
        {markers.map(({ internalId, title, position }) => (
          <MarkerComponent
            key={internalId}
            internalId={internalId}
            title={title}
            position={position}
            goHandler={() => {
              setCenter(position);
              setZoom(8);
            }}
            updateMarker={updateMarker}
          />
        ))}
      </MarkerList>
    </>
  );
};

export { MarkersIndicator };
