const removeFromArray = (internalId, array) => {
  const i = array.findIndex(
    (possibleElement) => possibleElement.internalId === internalId,
  );

  if (i === -1) return array; // Element not found returning original array

  return [...array.slice(0, i), ...array.slice(i + 1)];
};

const updateArrayElement = ({ internalId, position, title }, array) => {
  const i = array.findIndex(
    (possibleElement) => possibleElement.internalId === internalId,
  );

  if (i === -1) return array; // Element not found returning original array

  const modifyedElement = { ...array[i] };
  if (position) {
    modifyedElement.position = position;
  }

  if (title) {
    modifyedElement.title = title;
  }

  return [...array.slice(0, i), modifyedElement, ...array.slice(i + 1)];
};

export const MarkersReducer = (state, { type, data }) => {
  switch (type) {
    case 'ADD_MARKER':
      return { data: [...state.data, data] };
    case 'REMOVE_MARKER':
      return {
        data: removeFromArray(data, state.data),
      };
    case 'UPDATE_MARKER':
      return {
        data: updateArrayElement(data, state.data),
      };
    case 'RESET_DATA':
      return { data };
    default:
      throw new Error();
  }
};
